//npm i axios
import Layout from "../common/Layout";
import axios from "axios";
import { useEffect, useState } from "react";

export default function Department() {
    // 
    const path = process.env.PUBLIC_URL;
    // members 

    // useState  []
    const [Geobug, setGeobug] = useState([]);
    const [Members, setMembers] = useState([]);

    //   
    useEffect(() => {
        // members.json   json 
        axios.get(`${path}/DB/geobug.json`).then((json) => {
            //setMembers  
            setGeobug(json.data.geobug);
        });
        axios.get(`${path}/DB/members.json`).then((json) => {
            //setMembers  
            setMembers(json.data.members);
        });
    }, []);

    return (
        <Layout name={'Department'}>
            <h2>Dancers</h2>
            <p>LACHICA</p>
            <div className="img">
                <img src={`${path}/img/dp01.jpg`} />
            </div>
            <p></p>
            <div className="img">
                <img src={`${path}/img/dp02.jpg`} />
            </div>
            <p>DANCER</p>
            {/* Members   */}
            {Members.map((data, index) => {
                //  
                return (
                    <article key={index}>
                        <div className="inner">
                            <div className="pic">
                                <img src={`${path}/img/${data.pic}`} alt={data.name} />
                            </div>
                            <h3>{data.name}</h3>
                            <p className="name">{data.krName}</p>
                        </div>
                    </article>
                );
            })}
            {Geobug.map((data, index) => {
                //  
                return (
                    <article key={index}>
                        <div className="inner">
                            <div className="pic">
                                <img src={`${path}/img/${data.pic}`} alt={data.name} />
                            </div>
                            <h3>{data.name}</h3>
                            <p className="name">{data.krName}</p>
                        </div>
                    </article>
                );
            })}


        </Layout>
    );
}